/* ... Otros estilos ... */

.fondo-azul {
    background-color: #9acef8;
}

#banner {
    text-align: center;
    padding: 10px;
    background-color: #2196f3;
    max-width: 100%; /* Ajusta el ancho máximo del banner */
    overflow: hidden; /* Oculta el contenido si es demasiado grande */
}

#banner img {
    max-width: 100%; /* Ajusta el ancho máximo de la imagen */
    height: auto;
}

#contenido-centro {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    max-width: 300px; /* Ajusta el ancho máximo según tus necesidades */
    margin: 0 auto;
}

#contenido-centro img {
    max-width: 100%;
    height: auto;
}

#btnInicio {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: white;
    color: #2196f3;
    border: none;
    cursor: pointer;
    display: block;
    margin: 20px auto; /* Ajusta el margen superior e inferior según tus necesidades */
    width: 80%;
    max-width: 200px; /* Ajusta el ancho máximo según tus necesidades */
}
